'use client';

import { CRMProvider } from '@/hooks/useCRMStore';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Card } from '@/components/ui/card';

const navigation = [
  { name: 'Contactos', href: '/crm/contacts' },
  { name: 'Oportunidades', href: '/crm/opportunities' },
  { name: 'Actividades', href: '/crm/activities' },
];

export default function CRMLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname();

  return (
    <CRMProvider>
      <div className="min-h-screen bg-slate-50">
        {/* Header */}
        <header className="bg-white border-b border-slate-200">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex justify-between items-center h-16">
              <div className="flex items-center space-x-4">
                <Link href="/" className="text-2xl font-bold text-slate-900">
                  CRM Simple
                </Link>
                <div className="hidden md:block w-px h-6 bg-slate-300" />
                <nav className="hidden md:flex space-x-1">
                  {navigation.map((item) => {
                    const isActive = pathname === item.href;
                    return (
                      <Link key={item.name} href={item.href}>
                        <Button
                          variant={isActive ? "default" : "ghost"}
                          size="sm"
                          className={isActive ? "bg-slate-900 text-white" : "text-slate-600 hover:text-slate-900"}
                        >
                          {item.name}
                        </Button>
                      </Link>
                    );
                  })}
                </nav>
              </div>
              <Link href="/">
                <Button variant="outline" size="sm">
                  Inicio
                </Button>
              </Link>
            </div>
          </div>
        </header>

        {/* Mobile Navigation */}
        <div className="md:hidden bg-white border-b border-slate-200">
          <div className="px-4 py-3">
            <nav className="flex space-x-1">
              {navigation.map((item) => {
                const isActive = pathname === item.href;
                return (
                  <Link key={item.name} href={item.href} className="flex-1">
                    <Button
                      variant={isActive ? "default" : "ghost"}
                      size="sm"
                      className={`w-full ${isActive ? "bg-slate-900 text-white" : "text-slate-600"}`}
                    >
                      {item.name}
                    </Button>
                  </Link>
                );
              })}
            </nav>
          </div>
        </div>

        {/* Main Content */}
        <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          {children}
        </main>
      </div>
    </CRMProvider>
  );
}
