'use client';

import React, { createContext, useContext, useReducer, useEffect, ReactNode } from 'react';
import { 
  CRMState, 
  CRMAction, 
  Contact, 
  Opportunity, 
  Activity,
  generateId 
} from '@/lib/crmTypes';

// Initial state
const initialState: CRMState = {
  contacts: [],
  opportunities: [],
  activities: []
};

// Reducer function
function crmReducer(state: CRMState, action: CRMAction): CRMState {
  switch (action.type) {
    case 'LOAD_DATA':
      return action.payload;
    
    case 'ADD_CONTACT':
      return {
        ...state,
        contacts: [...state.contacts, action.payload]
      };
    
    case 'UPDATE_CONTACT':
      return {
        ...state,
        contacts: state.contacts.map(contact =>
          contact.id === action.payload.id ? action.payload : contact
        )
      };
    
    case 'DELETE_CONTACT':
      return {
        ...state,
        contacts: state.contacts.filter(contact => contact.id !== action.payload)
      };
    
    case 'ADD_OPPORTUNITY':
      return {
        ...state,
        opportunities: [...state.opportunities, action.payload]
      };
    
    case 'UPDATE_OPPORTUNITY':
      return {
        ...state,
        opportunities: state.opportunities.map(opportunity =>
          opportunity.id === action.payload.id ? action.payload : opportunity
        )
      };
    
    case 'DELETE_OPPORTUNITY':
      return {
        ...state,
        opportunities: state.opportunities.filter(opportunity => opportunity.id !== action.payload)
      };
    
    case 'ADD_ACTIVITY':
      return {
        ...state,
        activities: [...state.activities, action.payload]
      };
    
    case 'DELETE_ACTIVITY':
      return {
        ...state,
        activities: state.activities.filter(activity => activity.id !== action.payload)
      };
    
    default:
      return state;
  }
}

// Context type
interface CRMContextType {
  state: CRMState;
  dispatch: React.Dispatch<CRMAction>;
  addContact: (contact: Omit<Contact, 'id' | 'createdAt' | 'updatedAt'>) => void;
  updateContact: (contact: Contact) => void;
  deleteContact: (id: string) => void;
  addOpportunity: (opportunity: Omit<Opportunity, 'id' | 'createdAt' | 'updatedAt'>) => void;
  updateOpportunity: (opportunity: Opportunity) => void;
  deleteOpportunity: (id: string) => void;
  addActivity: (activity: Omit<Activity, 'id' | 'createdAt'>) => void;
  deleteActivity: (id: string) => void;
  searchContacts: (query: string) => Contact[];
  getContactsByCompany: (company: string) => Contact[];
  getContactsByTag: (tag: string) => Contact[];
  getOpportunitiesByStatus: (status: string) => Opportunity[];
  getActivitiesByType: (type: string) => Activity[];
  getActivitiesByDateRange: (startDate: Date, endDate: Date) => Activity[];
}

// Context
const CRMContext = createContext<CRMContextType | null>(null);

// Provider component
export function CRMProvider({ children }: { children: ReactNode }) {
  const [state, dispatch] = useReducer(crmReducer, initialState);

  // Load data from localStorage on mount
  useEffect(() => {
    const savedData = localStorage.getItem('crm-data');
    if (savedData) {
      try {
        const parsedData = JSON.parse(savedData);
        // Convert date strings back to Date objects
        const processedData: CRMState = {
          contacts: parsedData.contacts?.map((contact: any) => ({
            ...contact,
            createdAt: new Date(contact.createdAt),
            updatedAt: new Date(contact.updatedAt)
          })) || [],
          opportunities: parsedData.opportunities?.map((opportunity: any) => ({
            ...opportunity,
            estimatedClosingDate: new Date(opportunity.estimatedClosingDate),
            createdAt: new Date(opportunity.createdAt),
            updatedAt: new Date(opportunity.updatedAt)
          })) || [],
          activities: parsedData.activities?.map((activity: any) => ({
            ...activity,
            date: new Date(activity.date),
            createdAt: new Date(activity.createdAt)
          })) || []
        };
        dispatch({ type: 'LOAD_DATA', payload: processedData });
      } catch (error) {
        console.error('Error loading CRM data from localStorage:', error);
      }
    }
  }, []);

  // Save data to localStorage whenever state changes
  useEffect(() => {
    localStorage.setItem('crm-data', JSON.stringify(state));
  }, [state]);

  // Helper functions
  const addContact = (contactData: Omit<Contact, 'id' | 'createdAt' | 'updatedAt'>) => {
    const now = new Date();
    const contact: Contact = {
      ...contactData,
      id: generateId(),
      createdAt: now,
      updatedAt: now
    };
    dispatch({ type: 'ADD_CONTACT', payload: contact });
  };

  const updateContact = (contact: Contact) => {
    const updatedContact = {
      ...contact,
      updatedAt: new Date()
    };
    dispatch({ type: 'UPDATE_CONTACT', payload: updatedContact });
  };

  const deleteContact = (id: string) => {
    dispatch({ type: 'DELETE_CONTACT', payload: id });
  };

  const addOpportunity = (opportunityData: Omit<Opportunity, 'id' | 'createdAt' | 'updatedAt'>) => {
    const now = new Date();
    const opportunity: Opportunity = {
      ...opportunityData,
      id: generateId(),
      createdAt: now,
      updatedAt: now
    };
    dispatch({ type: 'ADD_OPPORTUNITY', payload: opportunity });
  };

  const updateOpportunity = (opportunity: Opportunity) => {
    const updatedOpportunity = {
      ...opportunity,
      updatedAt: new Date()
    };
    dispatch({ type: 'UPDATE_OPPORTUNITY', payload: updatedOpportunity });
  };

  const deleteOpportunity = (id: string) => {
    dispatch({ type: 'DELETE_OPPORTUNITY', payload: id });
  };

  const addActivity = (activityData: Omit<Activity, 'id' | 'createdAt'>) => {
    const activity: Activity = {
      ...activityData,
      id: generateId(),
      createdAt: new Date()
    };
    dispatch({ type: 'ADD_ACTIVITY', payload: activity });
  };

  const deleteActivity = (id: string) => {
    dispatch({ type: 'DELETE_ACTIVITY', payload: id });
  };

  // Search and filter functions
  const searchContacts = (query: string): Contact[] => {
    const lowercaseQuery = query.toLowerCase();
    return state.contacts.filter(contact =>
      contact.name.toLowerCase().includes(lowercaseQuery) ||
      contact.email.toLowerCase().includes(lowercaseQuery) ||
      contact.company.toLowerCase().includes(lowercaseQuery) ||
      contact.tags.some(tag => tag.toLowerCase().includes(lowercaseQuery))
    );
  };

  const getContactsByCompany = (company: string): Contact[] => {
    return state.contacts.filter(contact => 
      contact.company.toLowerCase() === company.toLowerCase()
    );
  };

  const getContactsByTag = (tag: string): Contact[] => {
    return state.contacts.filter(contact =>
      contact.tags.some(t => t.toLowerCase() === tag.toLowerCase())
    );
  };

  const getOpportunitiesByStatus = (status: string): Opportunity[] => {
    return state.opportunities.filter(opportunity => opportunity.status === status);
  };

  const getActivitiesByType = (type: string): Activity[] => {
    return state.activities.filter(activity => activity.type === type);
  };

  const getActivitiesByDateRange = (startDate: Date, endDate: Date): Activity[] => {
    return state.activities.filter(activity => {
      const activityDate = new Date(activity.date);
      return activityDate >= startDate && activityDate <= endDate;
    });
  };

  const value: CRMContextType = {
    state,
    dispatch,
    addContact,
    updateContact,
    deleteContact,
    addOpportunity,
    updateOpportunity,
    deleteOpportunity,
    addActivity,
    deleteActivity,
    searchContacts,
    getContactsByCompany,
    getContactsByTag,
    getOpportunitiesByStatus,
    getActivitiesByType,
    getActivitiesByDateRange
  };

  return React.createElement(CRMContext.Provider, { value }, children);
}

// Hook to use CRM context
export function useCRMStore() {
  const context = useContext(CRMContext);
  if (!context) {
    throw new Error('useCRMStore must be used within a CRMProvider');
  }
  return context;
}
